'use client';

import React, { createContext, useContext, useState, useEffect } from 'react';

export type Status = 'Completed' | 'In Progress' | 'Not Started';

export interface Milestone {
  id: string;
  title: string;
  description: string;
  status: Status;
  technologies: string[];
  github?: string;
  month: string;
  projectId: string;
}

interface MilestoneContextType {
  milestones: Milestone[];
  stats: {
    completed: number;
    inProgress: number;
    notStarted: number;
    total: number;
    completedPercentage: number;
  };
  addMilestone: (milestone: Omit<Milestone, 'id'>) => void;
  removeMilestone: (id: string) => void;
  updateMilestone: (id: string, updates: Partial<Milestone>) => void;
  updateMilestoneStatus: (id: string, status: Status) => void;
}

const MilestoneContext = createContext<MilestoneContextType | undefined>(undefined);

export function MilestoneProvider({ children }: { children: React.ReactNode }) {
  const [milestones, setMilestones] = useState<Milestone[]>([]);

  // Load milestones from localStorage on initial render
  useEffect(() => {
    try {
      const savedMilestones = localStorage.getItem('milestones');
      if (savedMilestones) {
        setMilestones(JSON.parse(savedMilestones));
      }
    } catch (error) {
      console.error('Error loading milestones from localStorage:', error);
    }
  }, []);

  // Save milestones to localStorage whenever they change
  useEffect(() => {
    try {
      localStorage.setItem('milestones', JSON.stringify(milestones));
    } catch (error) {
      console.error('Error saving milestones to localStorage:', error);
    }
  }, [milestones]);

  // Calculate statistics
  const stats = {
    completed: milestones.filter(m => m.status === 'Completed').length,
    inProgress: milestones.filter(m => m.status === 'In Progress').length,
    notStarted: milestones.filter(m => m.status === 'Not Started').length,
    total: milestones.length,
    completedPercentage: milestones.length > 0 
      ? Math.round((milestones.filter(m => m.status === 'Completed').length / milestones.length) * 100)
      : 0,
  };

  const addMilestone = (milestone: Omit<Milestone, 'id'>) => {
    setMilestones(prev => [...prev, { ...milestone, id: `milestone-${Date.now()}` }]);
  };

  const removeMilestone = (id: string) => {
    setMilestones(prev => prev.filter(m => m.id !== id));
  };

  const updateMilestone = (id: string, updates: Partial<Milestone>) => {
    setMilestones(prev => 
      prev.map(m => m.id === id ? { ...m, ...updates } : m)
    );
  };

  const updateMilestoneStatus = (id: string, status: Status) => {
    setMilestones(prev => 
      prev.map(m => m.id === id ? { ...m, status } : m)
    );
  };

  return (
    <MilestoneContext.Provider value={{
      milestones,
      stats,
      addMilestone,
      removeMilestone,
      updateMilestone,
      updateMilestoneStatus,
    }}>
      {children}
    </MilestoneContext.Provider>
  );
}

export function useMilestones() {
  const context = useContext(MilestoneContext);
  if (context === undefined) {
    throw new Error('useMilestones must be used within a MilestoneProvider');
  }
  return context;
} 